.profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    background-image: url('../../../public/background/main.png');
    background-size: cover;
    position: relative; /* Necesario para que los elementos se posicionen de forma adecuada */
}

.profile-header {
    width: 100%;
    max-width: 600px;
    margin: 0;
    position: absolute; /* Lo coloca en la parte superior */
    top: 0; /* Asegura que esté pegado al top */
    left: 0;
    z-index: 10; /* Asegura que el header esté por encima del contenido */
}

.profile-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-top: 80px; /* Espacio para que el contenido no quede debajo del header */
}

.profile-input {
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
    text-align: center;
    width: 250px;
}

button {
    cursor: pointer;
    width: 190px;
    height: 50px;
    padding: 0;
    border: none;
    background: none;
    position: relative;
}

button:focus {
    outline: none;
    box-shadow: none;
    background: none;
    border: none;
}

button {
    -webkit-tap-highlight-color: transparent;
}

.send-button {
    width: 100%;
}

.button-text {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 16px;
    font-weight: bold;
    font-family: cinzel;
    pointer-events: none;
}
